.app__footer {
    flex: 1;
    width: 100%;
    flex-direction: column;
  
    @media screen and (max-width: 768px) {
      flex-direction: column;
    }
  }
  .head-text{
    color: var(--title-color);
  }
  
  .app__footer-cards {
    margin-left: auto;
    margin-right: auto;
    margin-top: 4rem;
    margin-bottom: 2rem;
    width: 60%;
    display: flex;
    position: relative;
    bottom: 45px;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    // margin: 4em 2rem 2rem;
  
    .app__footer-card {
      min-width: 290px;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
  
      margin: 1rem 0;
      padding: 1rem;
      border-radius: 10px;
      cursor: pointer;
      background-color: #f8e7e8;
  
      transition: all 0.3s ease-in-out;
  
      img {
        width: 40px;
        height: 40px;
        margin: 0 0.7rem;
      }
  
      p {
        font-weight: 500;
      }
      a {
        color: rgb(91, 88, 88);
        font-size: small;
        text-decoration: none;
        font-weight: 500;
      }
      &:hover {
        box-shadow: 0 0 25px #c1babb;
      }
  
      @media screen and (max-width: 450px) {
        width: 100%;
      }
    }
  
    @media screen and (max-width: 768px) {
      width: 80%;
    }
  }
  
  .app__footer-cards .app__footer-card:last-child {
    background-color: #d4e2ee;
  }
  
  .app__footer-cards .app__footer-card:last-child:hover {
    box-shadow: 0 0 25px #bfd9ee;
  }
  
  .app__footer-form {
    margin-left: auto;
    margin-right: auto;
    
      position: relative;
      bottom: 80px;
    width: 60%;
    flex-direction: column;
    // margin: 1rem 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
  
    div {
      width: 100%;
  
      margin: 0.75rem 0;
      border-radius: 10px;
      cursor: pointer;
      background-color: var(--primary-color);
  
      transition: all 0.3s ease-in-out;
  
      input,
      textarea {
        width: 100%;
        padding: 0.95rem;
        border: none;
        border-radius: 7px;
        background-color: #edf2f8;
  
        font-family: var(--font-base);
        color: var(--secondary-color);
        outline: none;
      }
  
      textarea {
        height: 170px;
      }
  
      &:hover {
        box-shadow: 0 0 25px rgb(217, 199, 199);
      }
    }
  
    button {
        display: flex;
        justify-content: center;
        align-items: center;
      padding: 1rem 2rem;
      border-radius: 10px;
      border: none;
      background-color: var(--title-color);
  
      font-weight: 500;
      color: var(--container-color);
      outline: none;
      margin: 2rem 0 0 0;
      font-family: var(--font-base);
  
      // transition: cubic-bezier(0.55, 0.085, 0.68, 0.53);
      transition: all .5s ease-in-out;
      cursor: pointer;
  
      &:hover {
        background-color: #2430af;
      }
    }
  
    @media screen and (max-width: 768px) {
      width: 80%;
      margin-left: auto;
      margin-right: auto;
    //   margin: 1rem 0;
    }
  }